import { cva, cx } from 'class-variance-authority';
import { MouseEventHandler, useState } from 'react';
import { TooltipBubbleProps, TooltipProps } from './tooltip.interface';

const tooltipBubble = cva(
  'zep-absolute zep-flex-col zep-w-full zep-min-w-fit zep-box-border sm:zep-w-fit zep-whitespace-nowrap zep-drop-shadow-md  zep-typography-supportText zep-rounded-4',
  {
    variants: {
      show: {
        true: 'zep-flex sm:zep-hidden sm:group-hover:zep-flex',
        false: 'zep-hidden sm:zep-hidden sm:group-hover:zep-flex',
      },
      align: {
        start: 'zep-items-start zep-justify-start',
        center: 'zep-items-center zep-justify-center',
        end: 'zep-items-end zep-justify-end',
      },
      position: {
        top: 'zep-bottom-[calc(100%_+_4px)]',
        right: 'zep-left-[calc(100%_+_14px)]',
        bottom: 'zep-top-[calc(100%_+_4px)]',
        left: 'zep-right-[calc(100%_+_14px)]',
      },
      color: {
        light: 'zep-bg-greyscale-0 zep-text-typography-dark-100',
        dark: 'zep-bg-greyscale-900 zep-text-typography-light-100',
      },
    },
    defaultVariants: {
      align: 'center',
      color: 'dark',
    },
  },
);

const tooltipCarret = cva('zep-flex zep-absolute zep-box-border', {
  variants: {
    align: {
      start: '',
      center: '',
      end: '',
    },
    position: {
      top: 'zep-top-[100%]',
      right: 'zep-right-[calc(100%_-_11px)] zep-rotate-90',
      bottom: 'zep-bottom-[100%] zep-rotate-180',
      left: 'zep-left-[calc(100%_-_11px)] -zep-rotate-90',
    },
    color: {
      light: 'zep-text-typography-light-100',
      dark: 'zep-text-typography-dark-100',
    },
  },
  compoundVariants: [
    {
      align: 'start',
      position: ['left', 'right'],
      className: 'zep-bottom-[calc(100%_-_12px)]',
    },
    {
      align: 'end',
      position: ['left', 'right'],
      className: 'zep-top-[calc(100%_-_12px)]',
    },
  ],
  defaultVariants: {
    position: 'top',
    color: 'dark',
  },
});

const XMark = () => (
  <svg width="14" height="14" viewBox="0 0 14 14" fill="none" xmlns="http://www.w3.org/2000/svg">
    <path
      d="M14 1.41L12.59 0L7 5.59L1.41 0L0 1.41L5.59 7L0 12.59L1.41 14L7 8.41L12.59 14L14 12.59L8.41 7L14 1.41Z"
      fill="currentColor"
    />
  </svg>
);

const Caret = () => (
  <svg width="26" height="5" viewBox="0 0 26 5" fill="none" xmlns="http://www.w3.org/2000/svg">
    <path d="M8 0L12.2929 4.29289C12.6834 4.68342 13.3166 4.68342 13.7071 4.29289L18 0H8Z" fill="currentColor" />
  </svg>
);

const TooltipBubble = ({ className, children, position, color, align, onClose, show }: TooltipBubbleProps) => {
  return (
    <div className={cx(tooltipBubble({ align, color, position, show }), className)}>
      <button className="zep-flex sm:zep-hidden zep-self-end zep-m-0.5" onClick={onClose}>
        <XMark />
      </button>
      <span className="zep-flex zep-py-0.25 zep-w-full zep-p-0.75 sm:zep-px-1 sm:zep-py-0.25">{children}</span>
      <div className={tooltipCarret({ position, color, align })}>
        <Caret />
      </div>
    </div>
  );
};

export const Tooltip = ({
  children,
  content,
  className,
  color = 'dark',
  position = 'top',
  align = 'center',
}: TooltipProps) => {
  const [show, setShow] = useState(false);

  const handleOnClose: MouseEventHandler<HTMLDivElement> = (event) => {
    event.stopPropagation();
    setShow(false);
  };

  return (
    <div
      className={cx('zep-inline-flex zep-relative zep-justify-center zep-items-center zep-group', className)}
      data-testid="zep-tooltip"
      onClick={() => setShow(true)}
    >
      <TooltipBubble color={color} position={position} align={align} show={show} onClose={handleOnClose}>
        {content}
      </TooltipBubble>
      <div>{children}</div>
    </div>
  );
};
